import React from 'react';
import Swiper from "react-id-swiper"
import OwlCard from './owl_card'

export default function Owl() {
    const params = {
        slidesPerView: 5,
        spaceBetween: 50,
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        },
        breakpoints: {
          1024: {
            slidesPerView: 4,
            spaceBetween: 40
          },
          768: {
            slidesPerView: 3,
            spaceBetween: 30
          },
          640: {
            slidesPerView: 2,
            spaceBetween: 20
          },
          320: {
            slidesPerView: 1,
            spaceBetween: 10
          }
        }
      }
  
    return (
        <Swiper {...params}>
            <div>
                <OwlCard/>
            </div>
            <div>
                <OwlCard/>
            </div>
            <div>
                <OwlCard/>
            </div>
            <div>
                <OwlCard/>
            </div>
            <div>
                <OwlCard/>
            </div>
        </Swiper>
    )
}
